@keyframes start{
    0%{ transform: translateY(-80vw); }
    100%{ transform: translateY(0px) }
}
.titel{
    height: 50px;
    width: 99vw;
    position: relative;
    display: flex;
    justify-content: center;
}

.titel_1, .titel_2, .titel_3{
    width: 400px;
    position: absolute;
    top: 5px;
    backface-visibility: hidden;
}
.titel_1{
    transform: rotateY(0deg) translateX(0) translateZ(0) translateZ(175px);
}
.titel_2{
    transform: rotateY(-120deg) translateX(0) translateZ(0) translateZ(175px);
}
.titel_3{
    transform: rotateY(-240deg) translateX(0) translateZ(0) translateZ(175px);
}
.titel{
    animation: rotate 40s infinite;
    transform-style: preserve-3d;
    transform: rotateX(25deg);
}
@keyframes rotate{
    0%{ transform: rotateY(0deg); }
    30%{ transform: rotateY(0deg); }
    33%{ transform: rotateY(120deg)}
    63%{ transform: rotateY(120deg)}
    66%{ transform: rotateY(240deg)}
    97%{ transform: rotateY(240deg); }
    100%{ transform: rotateY(360deg); }
}



/** Project section **/
.projects{
    text-align: center;
    background-image: linear-gradient(to bottom, rgba(255, 253, 253, 0), #eee);
    min-height: 100vh;
    padding: 20vh 0;
    color: #ddd;
}

.project_card_content a{
    color: rgb(170, 170, 170);
    font-size: 1.4rem;
    text-decoration: none;
}

.project_info{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.project_info p{
    width: 100%;
}

.my_project{
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.project_info div{
    width: 250px;
}

.project_img_container{
    width: 300px;
    height: 250px;
    overflow: hidden;
    border-radius: 25px;
}
.project_img_container img{

    height: 250px;
}
.project_card_content{
    position: relative;
    height: 330px;
    width: 330px;
    margin: 20px;
}
.project_links{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: space-between;
    left: 40px;
    width: 250px;
    bottom: 20px;
    color: #eee;
}
.project_links a{
    color: #eee;
    padding: 5px;
    border-radius: 10px;
    border: 1px solid rgba(238, 238, 238, 0.6);
    transition: all 0.5s ease-in-out;
}
.project_links a:hover{
    background-color: rgba(238, 238, 238, 0.4);;
}

.project_card_front, .project_card_back{
    position: absolute;
    height: 330px;
    width: 330px;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    border-radius: 5%;
    background-image: linear-gradient(90deg, rgba(0, 0, 0, 0.5), rgba(71, 71, 71, 0.5));
    color: rgb(200, 200, 200);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transform-style: preserve-3d;
    transition: transform 0.7s ease-in-out;
    backface-visibility: hidden;
}

.project_card_back{
    transform: rotateY(180deg);
}

.project_card:hover .project_card_back{
    transform: rotateY(0deg);
}
.project_card:hover .project_card_front{
    transform: rotateY(180deg);
}

.about{
    /*text-align: center;*/
    font-size: larger;
    margin: 0 auto;
    background: #eee;
    min-height: 100vh;
    padding: 35vh 0;
}
.about_content{
    margin: 0 auto;
    padding: 20px;
    max-width: 600px;
}

.about h2{
    text-align: center;
    text-decoration: underline;
}

.contact{
    text-align: center;
    padding: 2vh 0;
    background-image:  linear-gradient(to right, rgba(25, 25, 25, 0.5), rgba(25, 25, 25, 0.2));
}
.maillink_bottom{
    text-align: center;
}
.logolink_bottom{
    color: #333;
    transition: transform 2s;
}
.logolink_bottom:hover{
    transform: rotate3d(0,1,0,720deg) scale(1.5);
}

@media screen and (max-width: 1100px){
    .my_project{
        flex-direction: column;
    }
    .project{
        width: 90vw;
    }
    .project a{
        font-size: 1rem;
    }
    .project div{
        width: 100%;
    }
    .project p{
        width: 100%;
    }
}

